<template>
  <div id="app">
    <HomePage :colorPrimary="colorPrimary" :colorSecondary="colorSecondary" :colorThird="colorThird"></HomePage>
  </div>
</template>

<script>
import HomePage from '@/pages/HomePage'
export default {
  name: 'App',
  components: {
    HomePage
  },
  data() {
    return {
      colorPrimary: '#1469FF',
      colorSecondary: '#0027C8',
      colorThird: '#FF3C82'
    }
  }
}
</script>

<style>
#app {
}
</style>



