<script>
export default {
  name: 'cRect',
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    },
    bgColor: {
      type: String,
      default: '#000'
    }
  },
  data() {
    return {
      styleList: {
        width: `${this.width}px`,
        height: `${this.height}px`,
        backgroundColor: this.bgColor
      }
    }
  }
}
</script>

<template src="./template.html"></template>
<style lang="scss" src="./style.scss" scoped></style>



