<script>
export default {
  name: 'cTriangle',
  props: {
    base: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    },
    bgColor: {
      type: String,
      default: '#000'
    }
  },
  data() {
    return {
      styleList: {
        width: 0,
        height: 0,
        borderLeft: `${this.base / 2}px solid transparent`,
        borderRight: `${this.base / 2}px solid transparent`,
        borderBottom: `${this.height}px solid ${this.bgColor}`
      }
    }
  }
}
</script>

<template src="./template.html"></template>
<style lang="scss" src="./style.scss" scoped></style>



