<script>
export default {
  name: 'cCircle',
  props: {
    radius: {
      type: Number,
      required: true
    },
    bgColor: {
      type: String,
      default: '#000'
    }
  },
  data() {
    return {
      styleList: {
        width: `${this.radius * 2}px`,
        height: `${this.radius * 2}px`,
        borderRadius: '50%',
        backgroundColor: this.bgColor
      }
    }
  }
}
</script>

<template src="./template.html"></template>
<style lang="scss" src="./style.scss" scoped></style>



