<script>
import cRect from '@/components/cRect'
import cCircle from '@/components/cCircle'
import cTriangle from '@/components/cTriangle'

export default {
  name: 'homePage',
  props: {
    colorPrimary: {
      type: String,
      required: true
    },
    colorSecondary: {
      type: String,
      required: true
    },
    colorThird: {
      type: String,
      required: true
    }
  },
  components: {
    cRect,
    cCircle,
    cTriangle
  },
  data() {
    return {
      openDuration: 5,
      state: {
        open: true,
        q1: false,
        q2: false,
        q3: false
      }
    }
  },
  mounted() {
    let scaleRatio = 10
    let open = this.$el.querySelector('.openShow')
    let cRect = open.querySelector('.cRect')
    let cCircle = open.querySelector('.cCircle')
    let star = open.querySelector('.star')
    TweenLite.to(cRect, this.openDuration, {
      rotation: -360,
      ease: Power1.easeOut
    })
    TweenLite.to(cCircle, this.openDuration, {
      rotation: 360,
      ease: Power1.easeOut
    })
    TweenLite.to(cCircle, this.openDuration / 2, { css: { scale: 1.1 } })
    TweenLite.to(cCircle, this.openDuration / 2, {
      css: { scale: 1 },
      delay: this.openDuration / 2
    })
    TweenLite.to(star, this.openDuration, {
      rotation: 360,
      ease: Power1.easeOut
    })
    TweenLite.to(cRect, 1.2, {
      css: { scale: scaleRatio },
      delay: this.openDuration
    })
    TweenLite.to(cCircle, 1.2, {
      css: { scale: scaleRatio },
      delay: this.openDuration + 0.3
    })
    TweenLite.to(star, 0.75, {
      css: { scale: scaleRatio },
      delay: this.openDuration + 0.6,
      onComplete: this.openShowOver
    })
  },
  methods: {
    openShowOver() {
      this.state.open = false
    }
  }
}
</script>

<template src="./template.html"></template>
<style lang="scss" src="./style.scss" scoped></style>



